  display: block;
}

.mat-dialog-content::after {
  content: "";
  width: 90px;
  height: 90px;
  position: absolute;
  top: 0px;
  right: 20px;
  background: url("/assets/images/icons/banner.png");
  background-repeat: no-repeat;
  background-size: contain;
  z-index: -1;
}

.mat-dialog-actions {
  justify-content: flex-end;
}

/*
.cdk-overlay-container {
  z-index: 9999; // to show snackbars above loading
}
*/
.dialog-responsive {
  min-width: 35vw !important;
  width: 35vw !important;
  max-width: 35vw !important;
}

@media screen and (max-width: 1440px) {
  .dialog-responsive {
    min-width: 45vw !important;
    width: 45vw !important;
    max-width: 45vw !important;
  }
}
@media screen and (max-width: 1280px) {
  .dialog-responsive {
    min-width: 60vw !important;
    width: 60vw !important;
    max-width: 60vw !important;
  }
}
@media screen and (max-width: 960px) {
  .dialog-responsive {
    min-width: 75vw !important;
    width: 75vw !important;
    max-width: 75vw !important;
  }
}
@media screen and (max-width: 600px) {
  .dialog-responsive {
    min-width: 97.5vw !important;
    width: 97.5vw !important;
    max-width: 97.5vw !important;
  }
}
/*                                                                                      *
 *                                     DRAG AND DROP                                    *
 *                                                                                      */
.cdk-drag-placeholder {
  opacity: 0.5;
  box-sizing: border-box;
  border: 2px dashed #99009c;
}

.cdk-drag-animating,
.cdk-drop-list-dragging .cdk-drag {
  transition: transform 0.3s ease;
}

.cdk-drag-preview {
  opacity: 0.5;
  box-sizing: border-box;
  border: 2px dashed #99009c;
  display: block;
  height: 60px;
  width: 100%;
  padding: 0;
  position: relative;
  background-color: #424242;
  color: white;
  font-size: 12px;
}
.cdk-drag-preview.grey .mat-list-item-content, .cdk-drag-preview.common .mat-list-item-content {
  background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, #3e2723 50%, rgba(255, 255, 255, 0) 100%);
}
.cdk-drag-preview.black .mat-list-item-content {
  background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, #4a148c 50%, rgba(255, 255, 255, 0) 100%);
}
.cdk-drag-preview.red .mat-list-item-content {
  background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, #b71c1c 50%, rgba(255, 255, 255, 0) 100%);
}
.cdk-drag-preview.green .mat-list-item-content {
  background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, #1b5e20 50%, rgba(255, 255, 255, 0) 100%);
}
.cdk-drag-preview.blue .mat-list-item-content {
  background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, #0d47a1 50%, rgba(255, 255, 255, 0) 100%);
}
.cdk-drag-preview.white .mat-list-item-content {
  background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, #b0bec5 50%, rgba(255, 255, 255, 0) 100%);
}
.cdk-drag-preview.legendary .mat-list-avatar.mat-badge-before img.mat-list-avatar {
  box-shadow: 0px 0px 10px 5px darkgoldenrod;
  border-color: darkgoldenrod;
}
.cdk-drag-preview .mat-list-item-content {
  display: flex;
  flex-direction: row;
  align-items: center;
  box-sizing: border-box;
  padding: 0 16px;
  position: relative;
  height: inherit;
}
.cdk-drag-preview .mat-list-item-content .mat-list-avatar {
  flex-shrink: 0;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  object-fit: cover;
}
.cdk-drag-preview .mat-list-item-content .mat-list-avatar img.mat-list-avatar {
  box-sizing: content-box;
  border: 2px solid #99009c;
}
.cdk-drag-preview .mat-list-item-content .mat-list-text {
  display: flex;
  flex-direction: column;
  width: 100%;
  box-sizing: border-box;
  overflow: hidden;
  padding: 0;
  padding-right: 0;
  padding-left: 16px;
}
.cdk-drag-preview .mat-list-item-content .mat-list-text .mat-line {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  display: block;
  box-sizing: border-box;
  text-align: start;
  margin: 0;
  padding: 0;
  font-weight: normal;
  font-size: inherit;
  line-height: 1.25 !important;
}
.cdk-drag-preview .mat-list-item-content .mat-list-text .mat-line:nth-child(n+2) {
  font-size: 12px;
}

/*                                                                                      *
 *                                        SELECTS                                       *
 *                                                                                      */
.mat-select-panel .mat-option {
  height: 60px !important;
}

.mat-select-trigger .mat-select-value {
  overflow: visible;
}

/*                                                                                      *
 *                                       SPINNERS                                       *
 *                                                                                      */
.spinner-container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

/*                                                                                      *
 *                                         ICONS                                        *
 *                                                                                      */
.icon {
  width: 18px;
  height: 18px;
  border-radius: 50%;
  margin: 0;
  border: 1px solid #99009c;
  box-sizing: border-box;
  vertical-align: top;
}
.icon.grayscale {
  filter: grayscale(100%);
}

/*                                                                                      *
 *                                        BUTTONS                                       *
 *                                                                                      */
.button-container {
  display: inline-block;
  position: relative;
}

/*                                                                                      *
 *                                         MENUS                                        *
 *                                                                                      */
.flag-menu {
  display: flex !important;
  align-items: center;
}
.flag-menu img {
  margin-right: 10px;
}

/*                                                                                      *
 *                                        PANELS                                        *
 *                                                                                      */
.mat-expansion-panel-header.mat-expanded:focus,
.mat-expansion-panel-header.mat-expanded:hover {
  background: transparent !important;
}

/*                                                                                      *
 *                                       SNACKBARS                                      *
 *                                                                                      */
/*                                                                                      *
 *                                       TOOLTIPS                                       *
 *                                                                                      */
.mat-tooltip {
  white-space: pre-line;
  text-align: center;
  font-size: 0.85rem;
}

/*                                                                                      *
 *                                        HELPERS                                       *
 *                                                                                      */
.fill-space {
  flex: 1 1 auto;
}

.overflow {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  display: block;
  box-sizing: border-box;
}

hr {
  border-color: #99009c;
}

ul {
  list-style: none;
}

ul li::before {
  content: " ";
  color: #99009c;
  font-weight: bold;
  display: inline-block;
  width: 1em;
  margin-left: -1em;
}